import "../style.css"
import {
    practiceClips, initWorkerId, requestABlock, submitAns, generateCompletionCode,
    TEXT, surveyLink, clearWorkerId
} from '../../utils'
import { VideoQuestion, Panel, Modal } from '../../components'

    ; (async () => { // execute this async code after page is loaded

        initWorkerId()

        // new Set(practiceClips.map(p => p.id))

        const instructionPanel = new Panel({
            containerId: 'instruction',
            title: TEXT.instruction.title,
            content: TEXT.instruction.content,
            sticky: true,
        })

        const block = await requestABlock() // this function is defined in js/callAPIwithWorkerId.js
        console.log('fetch block', block)

        const numberOfClipPerPage = 10
        const pages = []
        for (let i = 0, len = block.clips.length; i < len; i += numberOfClipPerPage) {
            pages.push(block.clips.slice(i, i + numberOfClipPerPage))
        }
        let curPageIdx = Math.floor(block.curIdx / numberOfClipPerPage)
        let correctAttentionCheck = block.atCheck
        const blockId = block.id
        console.log(curPageIdx)

        // show the first page
        showPage(curPageIdx)

        let videoQuestions
        function showPage(pageIdx) {
            const page = pages[pageIdx]
            const container = document.getElementById('rootContainer')
            // clean the previous videos
            while (container.firstChild) {
                container.removeChild(container.lastChild)
            }

            window.scrollTo(0, 0);

            videoQuestions = page.map((d, idx) => {
                const clipIdx = pageIdx * numberOfClipPerPage + idx

                container.insertAdjacentHTML('beforeend', `<div class="flex-child" id="vq_${d}"></div>`)

                const videoQuestion = new VideoQuestion({
                    containerId: `vq_${d}`,
                    clipId: d,
                    clipIdx,
                    videoTitle: `Video ${clipIdx + 1} / ${block.clips.length}`,
                })

                // once play, pause others
                videoQuestion.onPlay(() => {
                    videoQuestions
                        .filter(v => v.clipId !== d)
                        .forEach(v => v.pauseVideo())
                })

                return videoQuestion
            })

        }

        /**
         * Add event handler to the submit button
         */
        document.querySelector('#btn_submit')
            .addEventListener('click', async () => {

                // check all answer
                // alert if some videos haven't been played
                if (videoQuestions.some(v => !v.hasPlayed)) {
                    const videosNotPlay = videoQuestions.filter(v => !v.hasPlayed).map(v => `Video ${v.clipIdx + 1}`)
                    alert(`You haven't watched these videos: ${videosNotPlay.join('\n')}`)
                    return
                }

                // alert if some videos haven no answer
                if (videoQuestions.some(v => v.ans === null)) {
                    const videosNotAnswer = videoQuestions.filter(v => !v.ans).map(v => `Video ${v.clipIdx + 1}`)
                    alert(`You haven't answered these videos: ${videosNotAnswer.join('\n')}`)
                    return
                }

                // show all answers
                const answers = videoQuestions.map(v => v.submitAns())
                console.log(answers)

                await submitAns({
                    blockId,
                    nextIdx: (curPageIdx + 1) * numberOfClipPerPage,
                    answers
                })

                // pause all video
                videoQuestions.forEach(v => v.pauseVideo())

                curPageIdx += 1
                if (curPageIdx < pages.length) {
                    showPage(curPageIdx)
                } else {
                    // finish all, get the code
                    const { code, showForm } = await generateCompletionCode()
                    // clear workerId after finish
                    clearWorkerId()

                    const content = showForm
                        ? `<p> Please complete the survey before returning to Mturk to enter your code</p>
                        <iframe id="google_iframe"
                            src="https://docs.google.com/forms/d/e/1FAIpQLSe9t8hHbSrIgZASN3n74T4Nxf7rXJt0k0Ghr-kycImbYQtGVA/viewform?usp=sf_link&entry.1593520845=${code}&embedded=true" 
                            style="overflow: auto;width: 800px;max-height: 1000px;min-height: 600px;"
                            frameborder="0" marginheight="0" marginwidth="0">
                            Loading…
                        </iframe>`
                        : `<p>You have finished this block of video clips. Your completion code is <strong id="code">${code}</strong>.</p> `

                    const succeedModal = new Modal({
                        containerId: 'succeedModal',
                        title: 'Well Done!',
                        content,
                        onClickButton: () => {
                            const textArea = document.createElement("textarea");
                            textArea.value = code;
                            document.body.appendChild(textArea);
                            textArea.select();
                            document.execCommand("Copy");
                            textArea.remove();
                            location.href = surveyLink
                        },
                        buttonTitle: 'Copy the code and Back to mturk',
                    })

                    // if need to fill the google form
                    if (showForm) {
                        let step = 0
                        // add event handler to iframe
                        document.getElementById('google_iframe')
                            .addEventListener('load', (e) => {
                                // finish filling
                                if (step === 1) {
                                    e.target.style.minHeight = '250px'
                                    succeedModal.appendContent(`<p>You have finished this block of video clips. Your completion code is <strong id="code">${code}</strong>.</p>`)
                                    succeedModal.showButton()

                                }
                                step++
                            })
                        succeedModal.hideButton()
                    }
                    // finish
                    succeedModal.show()
                }
            })
    })();
